import React, { Component } from "react";
import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  CardFooter,
  CardText,
  CardImg
} from "reactstrap";

const projetos = [
  {
    nome: "Portal CPTEC",
    descricao:
      "Novo portal para o Centro de Previso de Tempo e Estudos Climticos do INPE",
    img: require("../images/projetos/cptec.png"),
    linguagem: "Laravel | PHP",
    link: "https://www2.cptec.inpe.br/sp/cachoeira-paulista",
    open: true
  },
  {
    nome: "LIT",
    img: require("../images/projetos/lit.gif"),
    descricao: 'Plataforma onlearning da Saint Paul',
    linguagem: "React | React Native",
    link: 'https://www.lit.com.br/',
    open: false
  },
  {
    nome: "FISHBOT",
    img: require("../images/projetos/fish.png"),
    descricao: 'Aqurio semi-automatizado com controle e monitoramento remoto',
    linguagem: "Polymer",
    link: 'https://github.com/selcip/fishbot-dev',
    open: true
  },
  {
    nome: "SmartTV-rpi",
    img: require("../images/projetos/rpi.png"),
    descricao: 'Smart TV feita em javascript para o raspberry pi',
    linguagem: "Javascript",
    link: 'https://github.com/selcip/smarttv-rpi',
    open: true
  }
];

export default class Projetos extends Component {
  render() {
    return (
      <Container className="mb-3" id="projects">
        <Row>
          {projetos.map(projeto => (
            <Col key={projeto.nome} md={4} style={{ marginBottom: '.7rem' }}>
              <Card>
                <CardImg top width="250px" src={projeto.img} alt="Thumbnail" />
                <CardBody>
                  <CardTitle>
                    <a href={projeto.link} target="_blank">{projeto.nome}</a>
                  </CardTitle>
                  <CardText>{projeto.descricao}</CardText>
                </CardBody>
                <CardFooter className="text-muted clearfix">
                  <div className="float-left">
                    <i className="fas fa-code" /> {projeto.linguagem}
                  </div>
                  <div className="float-right">
                    {projeto.open ? (
                      <a href={projeto.link} target="_blank">
                        <i className="fab fa-github" /> Github
                      </a>
                    ) : (
                      "Closed Source"
                    )}
                  </div>
                </CardFooter>
              </Card>
            </Col>
          ))}
        </Row>
      </Container>
    );
  }
}



