import React, { Component } from "react";
import Navegacao from "./components/navbar";
import ColoredBg from "./components/coloredBg";
import Infos from "./components/infos";
import Tecnologias from "./components/tecnologias";
import Projetos from "./components/projetos";
import { Jumbotron, Button, Container } from "reactstrap";
import profile from "./images/profile.jpg";

class App extends Component {
  render() {
    return (
      <React.Fragment>
        <Navegacao />
        <Jumbotron
          id="aboutme"
          style={{
            position: "relative",
            marginTop: 56
          }}
          className="d-flex flex-column align-items-center text-white"
        >
          <div
            style={{
              zIndex: 2,
              textAlign: "center"
            }}
          >
            <img
              src={profile}
              className="img-fluid mb-2"
              alt="Avatar"
              style={{
                borderRadius: "50%",
                maxWidth: 150
              }}
            />
            <h2>Luiz Eduardo C.</h2>
            <p className="lead">Full-stack developer</p>
            <span role="img" aria-label="emoji" className="lead"></span>
            <hr style={{ borderColor: 'white' }} className="my-2" />
            <p className="lead">
              <Button
                color="primary"
                onClick={() => {
                  window.open("https://github.com/selcip", "_blank");
                }}
              >
                <i className="fab fa-github" />
              </Button>
            </p>
          </div>
          <ColoredBg />
        </Jumbotron>
        <Infos />
        <Container>
          <hr
            style={{
              marginTop: "2.5rem",
              marginBottom: "2.5rem"
            }}
          />
        </Container>
        <Tecnologias />
        <Projetos />
      </React.Fragment>
    );
  }
}

export default App;



