import React, { Component } from "react";
import { Jumbotron, Container, Row, Col, Badge } from "reactstrap";

export default class Tecnologias extends Component {
  render() {
    return (
      <Jumbotron id="tools" className="bg-primary text-white">
        <Container>
          <Row>
            <Col>
              <div className="tech">
                <header>
                  <i className="fas fa-laptop" />
                  <div className="titulo">Linguagens e ferramentas</div>
                </header>
                <h4>
                  <Badge color="secondary">Javascript</Badge>
                </h4>
                <h4>
                  <Badge color="secondary">CSS</Badge>
                </h4>
                <h4>
                  <Badge color="secondary">PHP</Badge>
                </h4>
                <h4>
                  <Badge color="secondary">C#</Badge>
                </h4>
                <h4>
                  <Badge color="secondary">MySQL</Badge>
                </h4>
                <h4>
                  <Badge color="secondary">Node.js</Badge>
                </h4>
              </div>
            </Col>
            <Col>
              <div className="tech">
                <header>
                  <i className="fas fa-wrench" />
                  <div className="titulo">Frameworks</div>
                </header>
                <h4>
                  <Badge color="secondary">React</Badge>
                </h4>
                <h4>
                  <Badge color="secondary">React Native</Badge>
                </h4>
                <h4>
                  <Badge color="secondary">Angular</Badge>
                </h4>
                <h4>
                  <Badge color="secondary">Ionic</Badge>
                </h4>
                <h4>
                  <Badge color="secondary">Laravel</Badge>
                </h4>
              </div>
            </Col>
            <Col>
              <div className="tech">
                <header>
                  <i className="fas fa-archive" />
                  <div className="titulo">Ferramentas</div>
                </header>
                <h4>
                  <Badge color="secondary">SASS</Badge>
                </h4>
                <h4>
                  <Badge color="secondary">Express</Badge>
                </h4>
                <h4>
                  <Badge color="secondary">Firebase</Badge>
                </h4>
              </div>
            </Col>
          </Row>
        </Container>
      </Jumbotron>
    );
  }
}



