import React, { Component } from "react";
import { Container, Row, Col, Badge } from "reactstrap";

export default class Infos extends Component {
  render() {
    return (
      <Container>
        <Row>
          <Col>
            <div className="info">
              <header>
                <i className="far fa-id-card" />
                <div className="titulo">Worked at</div>
              </header>
              <div className="info-text">
                <a href="http://lethus.com.br/">@lethus</a>
                <Badge pill>
                  <i className="far fa-clock" />2016
                </Badge>
              </div>
              <div className="info-text">
                <a href="https://www2.cptec.inpe.br/">@CPTEC/INPE</a>
                <Badge pill>
                  <i className="far fa-clock" />2017
                </Badge>
              </div>
              <div className="info-text">
                <a href="https://crosoften.com/">@crosoften</a>
                <Badge pill>
                  <i className="far fa-clock" />2018
                </Badge>
              </div>
            </div>
          </Col>
          <Col>
            <div className="info">
              <header>
                <i className="fas fa-graduation-cap" />
                <div className="titulo">Engenharia de Computao</div>
              </header>
              <div className="info-text">
                <a href="http://lethus.com.br/">UNISAL Lorena</a>
                <Badge pill>
                  <i className="far fa-clock" />2013-2017
                </Badge>
              </div>
            </div>
          </Col>
          <Col>
            <div className="info">
              <header>
                <i className="far fa-envelope" />
                <div className="titulo">Contato</div>
              </header>
              <div className="info-mail">
                <a href="mailto:eduselcip@gmail.com"> <i className="fas fa-edit"></i>{" "}Enviar um e-mail!</a>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    );
  }
}



