import React from "react";
import bgTop from "../images/bg-top.jpg";

const ColoredBg = () => (
  <div
    style={{
      position: "absolute",
      left: "0",
      top: "0",
      width: "100%",
      height: "100%",
      overflow: "hidden"
    }}
  >
    <div
      style={{
        position: "absolute",
        backgroundImage: `url(${bgTop})`,
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center center",
        left: "-1rem",
        top: "-1rem",
        width: "calc(100% + 1rem)",
        height: "calc(100% + 1rem)"
      }}
    />
    <div
      style={{
        background:
          "linear-gradient(rgb(0, 191, 165) 0%, rgba(49, 175, 178, 0.85) 71%, rgb(255, 255, 255) 100%)",
        opacity: 0.65,
        content: " ",
        position: "absolute",
        left: "0",
        top: "0",
        width: "100%",
        height: "100%"
      }}
    />
  </div>
);

export default ColoredBg



