import React, { Component } from "react";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink
} from "reactstrap";

class Navegacao extends Component {
  state = {
    isOpen: false
  };

  toggle = () => {
    this.setState({
      isOpen: !this.state.isOpen
    });
  };

  render() {
    return (
      <Navbar color="primary" fixed="top" dark expand="md">
        <NavbarBrand href="/">
          <span aria-label="logo" role="img"></span>
        </NavbarBrand>
        <NavbarToggler onClick={this.toggle} />
        <Collapse isOpen={this.state.isOpen} navbar>
          <Nav className="ml-auto" navbar>
            <NavItem>
              <NavLink href="#aboutme">Sobre mim</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="#tools">Linguagens e ferramentas</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="#projects">Projetos</NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    );
  }
}

export default Navegacao;



